@font-face {
    font-family: 'Roboto'; /*a name to be used later*/
    src: url('./Roboto-Regular.ttf'); /*URL to font*/
}
body{
    background-color: #ffffff ;
    /* background-color: #DDE5FF; */
    margin: 0 clamp(0rem, 10.87vw + -10.87rem, 10rem);

    font-family:'Roboto';
    height: 100vh;
}
.description{
    padding: 1rem;
}
.svg-map{
    /* width: 95%; */
    /* border-radius: 15px; */
    border-width: 1px 0px 1px 0px;
    border-style: solid;
    border-color: #0000003d;
}

/* .median_listing_price{
    color: red;
    background-color: red;
} */
#county-dropdown{
    /* display: flex; */
    /* align-items: center; */
    /* justify-content: center; */
}
.title{
    font-size: 1.7rem;
    font-weight: bold;
    margin-left: 1rem;
}
.titleDescription{
    margin-left: 1rem;
    
}

#county-dropdown select{
    /* width: 90%; */
    text-align: center;
    /* border-radius: 99px; */
    /* border: 1px solid #bbb; */
    font-size: 1.5rem;
    padding: 0.5rem;
    
    background-color: rgb(0, 0, 0, 0);
    /* background-color: red; */
    border: 1px solid rgba(0,0,0,0.1);
}
button{
    visibility: hidden;
    border: 0;
    
}
.map path{
    background-color: red;
    stroke-width: 0.5;
    stroke: rgb(0, 0, 0);
    pointer-events: fill;
    fill:  #f5f4f4;
    /* stroke-linecap: round;
    stroke-linejoin: round; */
}
#graphToolTip{
    position: absolute;
    visibility: hidden;
    z-index: 55;
}
.tooltip{
    position: absolute;
    visibility: hidden;
    background-color: rgb(0, 0, 0, 0.5);
    padding: 5px;
    color: white;
    z-index: 50;
    
}
.map path:hover{
    cursor: pointer;
}
hr{
    width: 10%;
    margin-bottom: 4rem;
    border: 1px solid rgba(0,0,0,0.15);
}
#content{
    
    position: relative;
    /* box-shadow: 0 4px 1px -2px rgb(0, 0, 0); */
}
.flexbox{
    flex-direction: column;
  grid-template-columns: 40% 40% 20%;
  justify-content: center;
  /* margin-top: 5rem; */
  height: 100%;
  display: grid;
    

}

.slider div, .slider2 div{
    text-align: center;
    padding-bottom: 1rem;
    
}
#scatterplot{
    height: 100%;
}
#scatterplot2 {
    height: 100%;
}
#plotContainer{
    display: flex;
    /* align-items: center; */
    flex-direction: column;
    border-width: 1px 1px 1px 1px; /* top right bottom left */
    height: 100%;
border-style: solid;
border-color: #0000003d;
}
.description_tile{
    border-radius: 0 0 3px 0;
    box-shadow: -3px 0px 0px 0px rgba(16, 66, 173, 0.75);
    padding-left: 0.5rem;
    margin-bottom: 1rem;
}
.description_tile:hover{
    cursor: pointer;
}
.cities-tooltip{
    position: relative;
    bottom: 0;
    
}
.description_title{
    text-align: center;
  font-size: 1.6rem;
}

.median_listing_price{
    height: 2rem;
    text-align: center;
    background-color: rgb(255, 255, 255);
    font-size: 1.5rem;
    color: #03c103;
}
.menu{
    display: flex;
    border-width: 0 0 0 1px;
    flex-direction: column;
    justify-items: center;
    padding-top: 2rem;

    border-style: solid;
    border-color: #0000003d;
}